.spinner__circle,
.spinner__circle:after {
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
}

.spinner__circle {
  margin: auto;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  border-top: 0.2rem solid var(--spinner__color-secondary);
  border-right: 0.2rem solid var(--spinner__color-secondary);
  border-bottom: 0.2rem solid var(--spinner__color-secondary);
  border-left: 0.2rem solid var(--spinner__color-primary);
  -webkit-animation: loading 1.1s infinite linear;
          animation: loading 1.1s infinite linear;
}

.spinner__circle--inline {
  border-top: 0.2rem solid var(--spinner__color-tertiary);
  border-right: 0.2rem solid var(--spinner__color-tertiary);
  border-bottom: 0.2rem solid var(--spinner__color-tertiary);
}

.spinner__container {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  margin: 1rem 1.75rem;
  height: 3rem;
  line-height: 3rem;
}

.spinner__container--inline {
  position: relative;
  margin: 0rem;
  background: transparent;
}

.spinner__text {
  color: var(--spinner__font-color);
  margin-left: 1rem;
  display: inline-block;
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}



