/* =========== TICKER =========== */
:root {
  /* /////////////// */
  /* SCROLLING TICKER WIDGET --start-- */
  /* TEXT & CONTENT */
  --ticker-text-font-words: 'Roboto', sans-serif;
  --ticker-text-font-numbers: var(--ticker-text-font-words);

  --ticker-session-high-text: 'High:';

  --ticker-icon--UP-trend-symbol: '\2B06';
  --ticker-icon--DOWN-trend-symbol: '\2B07';

  /* SIZES */
  --ticker-container-maxHeight: 30rem;

  --ticker-font-size-LG: 1.8rem;
  --ticker-font-size-MD: 1.2rem;

  --ticker-block-padding: 1rem 3.5rem 1.5rem 3.5rem;

  /* COLORS */
  --ticker-container-bg-color: #0d141d;

  /* falloff GRADIENT-LEFT-- use to edit this gradient: http://colorzilla.com/gradient-editor/#0d141d+0,000000+100&1+0,0+100 */
  --ticker-falloff-gradient-left-MOZ: -moz-linear-gradient(
    left,
    rgba(13, 20, 29, 1) 0%,
    rgba(0, 0, 0, 0) 100%
  ); /* FF3.6-15 */
  --ticker-falloff-gradient-left-WEBKIT: -webkit-linear-gradient(
    left,
    rgba(13, 20, 29, 1) 0%,
    rgba(0, 0, 0, 0) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  --ticker-falloff-gradient-left-LINEAR: linear-gradient(
    to right,
    rgba(13, 20, 29, 1) 0%,
    rgba(0, 0, 0, 0) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  --ticker-falloff-gradient-left-FILTER: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0d141d', endColorstr='#00000000',GradientType=1 ); /* IE6-9 */

  /* falloff GRADIENT-RIGHT-- use to edit this gradient: http://colorzilla.com/gradient-editor/#000000+0,0d141d+100&0+0,1+100 */
  --ticker-falloff-gradient-right-MOZ: -moz-linear-gradient(
    left,
    rgba(0, 0, 0, 0) 0%,
    rgba(13, 20, 29, 1) 100%
  ); /* FF3.6-15 */
  --ticker-falloff-gradient-right-WEBKIT: -webkit-linear-gradient(
    left,
    rgba(0, 0, 0, 0) 0%,
    rgba(13, 20, 29, 1) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  --ticker-falloff-gradient-right-LINEAR: linear-gradient(
    to right,
    rgba(0, 0, 0, 0) 0%,
    rgba(13, 20, 29, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  --ticker-falloff-gradient-right-FILTER: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#0d141d',GradientType=1 ); /* IE6-9 */

  --ticker-text--color-primary: #ffffff;
  --ticker-text--color-sub: #aaaaaa;
  --ticker-text--color-accent: #858585;

  --ticker-icon--UP-trend--color: lightgreen;
  --ticker-icon--DOWN-trend--color: lightcoral;
  /* SCROLLING TICKER WIDGET --end-- */
  /* /////////////// */
}

/* DEBUG COLORS */
/* .ticker-block:nth-of-type(odd) {background: rgba(176, 196, 222, 0.1);}
.ticker-block:first-of-type {background: red;}  */

[ap-widget~='ticker'] {
  background: var(--ticker-container-bg-color);
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-animation: tickerLoad 5s normal forwards ease-in-out;
  animation: tickerLoad 5s normal forwards ease-in-out;
  -webkit-user-select: none; /* Chrome all / Safari all */
  -moz-user-select: none; /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none;
  z-index: 99;
}

[ap-widget~='ticker'] .ticker-scrolling {
  position: relative;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  list-style: none;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  will-change: transform;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[ap-widget~='ticker'] .falloff {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  z-index: 1;
}
[ap-widget~='ticker'] .falloff div {
  padding-bottom: 2rem;
  height: 100%;
  width: 15rem;
  pointer-events: auto;
}

[ap-widget~='ticker'] .falloff .falloffRight {
  float: left;
  background: var(--ticker-falloff-gradient-left-MOZ);
  background: var(--ticker-falloff-gradient-left-WEBKIT);
  background: var(--ticker-falloff-gradient-left-LINEAR);
  -webkit-filter: var(--ticker-falloff-gradient-left-FILTER);
          filter: var(--ticker-falloff-gradient-left-FILTER);
}
[ap-widget~='ticker'] .falloff .falloffLeft {
  float: right;
  background: var(--ticker-falloff-gradient-right-MOZ);
  background: var(--ticker-falloff-gradient-right-WEBKIT);
  background: var(--ticker-falloff-gradient-right-LINEAR);
  -webkit-filter: var(--ticker-falloff-gradient-right-FILTER);
          filter: var(--ticker-falloff-gradient-right-FILTER);
}

.ticker {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  list-style: none;
}

.ticker-block {
  cursor: default;
  color: var(--ticker-text--color-primary);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: var(--ticker-block-padding);
  -webkit-transition: opacity 0.5s linear, -webkit-transform 0.3s ease-out;
  -o-transition: opacity 0.5s linear, -o-transform 0.3s ease-out;
  transition: opacity 0.5s linear, -webkit-transform 0.3s ease-out;
  -o-transition: opacity 0.5s linear, transform 0.3s ease-out;
  transition: opacity 0.5s linear, transform 0.3s ease-out;
  transition: opacity 0.5s linear, transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.ticker-block p {
  margin-bottom: 0.6rem;
}
.ticker-block span {
  display: -ms-flexbox;
  display: flex;
}

.ticker-block .ticker-session-high,
.ticker-block .ticker-symbol {
  font-size: var(--ticker-font-size-MD);
  font-weight: bold;
}

.ticker-block .ticker-symbol {
  font-family: var(--ticker-text-font-words);
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: var(--ticker-text--color-sub);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.ticker-block .ticker-rolling {
  font-size: 50%;
  position: absolute;
  top: 5rem;
  z-index: 100;
  width: 200%;
  color: var(--ticker-text--color-sub);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  opacity: 0;
  -webkit-transition: opacity 0.1s ease-in;
  -o-transition: opacity 0.1s ease-in;
  transition: opacity 0.1s ease-in;
}

.ticker-last-trade {
  font-size: var(--ticker-font-size-LG);
  font-family: var(--ticker-text-font-numbers);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.ticker-last-trade span {
  margin-left: 0.5rem;
  font-family: var(--ticker-text-font-words);
}

.high {
  color: var(--ticker-text--color-primary);
  font-family: var(--ticker-text-font-numbers);
}

.ticker-block .ticker-session-high,
.currency {
  color: var(--ticker-text--color-accent);
}

.ticker-block .ticker-session-high {
  font-weight: normal;
  display: -ms-flexbox;
  display: flex;
}

.ticker-block .ticker-session-high:before {
  font-family: var(--ticker-text-font-words);
  content: var(--ticker-session-high-text);
  margin-right: 0.5rem;
}

.trending_down {
  color: var(--ticker-icon--DOWN-trend--color);
  vertical-align: 'bottom';
  margin-left: 1rem;
}
.trending_down:before {
  content: var(--ticker-icon--DOWN-trend-symbol);
}

.trending_up {
  color: var(--ticker-icon--UP-trend--color);
  vertical-align: 'bottom';
  margin-left: 1rem;
}
.trending_up:before {
  content: var(--ticker-icon--UP-trend-symbol);
}

@-webkit-keyframes tickerLoad {
  0% {
    pointer-events: none;
    max-height: 0;
  }
  100% {
    pointer-events: all;
    max-height: var(--ticker-container-maxHeight);
  }
}
@keyframes tickerLoad {
  0% {
    pointer-events: none;
    max-height: 0;
  }
  100% {
    pointer-events: all;
    max-height: var(--ticker-container-maxHeight);
  }
}


/* BROWSER QUERIES */

/* IE11+ specific styles */
_:-ms-fullscreen, :root .ticker {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}


