.kyc-standalone__container {
  width: 70rem;
}

.kyc-standalone__section-header {
  margin-bottom: 2rem;
}

.kyc-standalone__section-body > div {
  width: 33%;
  display: inline-block;
  padding: 0 1.4rem;
}

.kyc-standalone__section-body > div:nth-child(3n - 2) {
  padding-left: unset;
}

.kyc-standalone__section-body > div:nth-child(3n) {
  padding-right: unset;
}


