.ap-modal {
  position: relative;
  overflow: hidden;
  background: var(--modal__bg-color);
  -webkit-box-shadow: 0 0 3.2rem 0 var(--shadow__color);
          box-shadow: 0 0 3.2rem 0 var(--shadow__color);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.ap-modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: var(--modal__overlay-color);
  z-index: var(--z-index__modal);
}

.ap-modal__close {
  display: none;
}

.ap-modal__close-icon {
  padding: 0.75rem;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  border-radius: 0 0 4px 4px;
  color: var(--font__color-secondary);
}

.ap-modal__close-icon:hover {
  opacity: 0.8;
}

.ap-modal__btn {
  padding: 0 5rem;
}

.ap-modal__header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 2.5rem;
  font-size: 1.4rem;
  color: var(--modal__header-color);
  background: var(--modal__header-bg-color);
}

.ap-modal__content {
  padding: 2.5rem;
}

.ap-modal__footer {
  padding: 2.5rem 0;
  margin-left: 2.5rem;
  border-top: 1px solid var(--border__color);
}



