.full-page-spinner__spinner,
.full-page-spinner__spinner:after {
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
}

.full-page-spinner__spinner {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  border-top: 1rem solid var(--spinner__color-secondary);
  border-right: 1rem solid var(--spinner__color-secondary);
  border-bottom: 1rem solid var(--spinner__color-secondary);
  border-left: 1rem solid var(--spinner__color-primary);
  -webkit-animation: loading 1.1s infinite linear;
          animation: loading 1.1s infinite linear;
}

.full-page-spinner__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: auto;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.full-page-spinner__overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  background: var(--body__gradient);
}

.full-page-spinner__text {
  padding-top: 1.5rem;
  color: var(--font__color-secondary);
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}



